import React from "react";
import { match } from "react-router";
import { t } from "@dashboard/application";

interface IProps {
    match: match<{
        id: string;
    }>;
}

export default class ArticlePage extends React.Component<IProps> {
    public render() {
        return (
            <div>
                <p>{t("Hello Article.")}</p>
                <p>
                    {t("The current slug is ")}
                    {this.props.match.params.id}
                </p>
            </div>
        );
    }
}
